@font-face {
  font-family: "Shink";
  src: local("Shink"), url(/WeddingWebsite/static/media/Shink.ae6c01c6.otf) format("opentype");
}

.cover-photo-container {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center; }
  .cover-photo-container .photo {
    height: 100%;
    width: 100%;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover; }
  .cover-photo-container .overlay {
    color: rgba(128, 128, 128, 0.5); }
  .cover-photo-container .content {
    display: flex;
    flex-direction: column;
    position: absolute; }
    .cover-photo-container .content .title {
      color: white;
      font-weight: bolder;
      font-family: "Shink", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
      font-size: 90px;
      text-align: center;
      z-index: 2;
      line-height: 1.3em; }
    .cover-photo-container .content .subtitle {
      color: white;
      font-family: "Tahoma", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
      font-size: 18px;
      text-align: center;
      z-index: 2;
      letter-spacing: 10px;
      text-transform: uppercase; }

